.icon-left {
  position: absolute;
  top: 10px;
  left: 10px;
}
.icon-right {
  position: absolute;
  bottom: 10px;
  right: 10px;
}
.season-display {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}
.season-display.winter i {
  color: blue;
}
.season-display.summer i {
  color: red;
}
.rightAlign {
  position: absolute;
  top: 10px;
  right: 10px;
}

.summer {
  background-color: orange;
}
.winter {
  background-color: rgb(171, 205, 235);
}
.fontSize {
  font-size: xx-large;
}
